.about-products {
  margin: auto;
  margin-bottom: 0.5rem;
  border: 1px solid rgba(69, 69, 192, 0.5);
  background-color: rgba(98, 195, 231, 0.5);
  height: 22rem;
  width: 16rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  border-radius: 1.2rem;
  box-shadow: 1px 1px 1px 1px rgba(129, 129, 173, 0.5);
}
.about-products > div {
  margin-bottom: 0.2rem;
}
.about-products .about-products_image {
  width: 100%;
  height: 10rem;
  min-height: 4rem;
}
.about-products .about-products_image img {
  width: 100%;
  height: 100%;
}
.about-products .about-products_title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: rgb(96, 96, 163);
  margin-bottom: 0.5rem;
  text-align: center;
}
.about-products .about-products_title h3 {
  margin-left: 0.2rem;
}
.about-products .about-products_title svg {
  background-color: rgb(66, 40, 184);
  padding: 0.25rem;
  width: 28px;
  height: 28px;
  opacity: 0.8;
  color: white;
  border-radius: 36px;
  margin-right: 0.1rem;
}
.about-products .about-products_text {
  text-align: center;
  font-size: 0.8rem;
  margin-bottom: 1rem;
  opacity: 0.6;
}
.about-products .about-products_button {
  background-color: rgba(98, 195, 231, 0.5);
  border: 1.5px solid rgba(87, 87, 194, 0.4);
  width: 75%;
  cursor: pointer;
  color: rgba(18, 90, 116, 0.6);
  font-weight: bold;
  border-radius: 2rem;
  text-align: center;
}